/* Start header section */
.header {
	display: flex;
	background-color: var(--header-footer-color);
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 100px;
	padding: 1% var(--outside-padding);
	position: absolute;
	z-index: 2;
}
.nav_list li {
	display: inline;
}

.nav_item {
	padding: 10px;
	font-size: x-large;
	text-decoration: none;
}
.nav_link{
	color: var(--contrast-color);
}
.nav_link:hover {
	font-weight: bolder;
	color: var(--highlight-color);
}
.active {
	font-weight: bolder;
	color: var(--highlight-color);
}

.logo_container {
	color: var(--contrast-color);
	text-decoration: none;
	font-size: xxx-large;
	font-weight: 900;
	transition: text-shadow 1s;
}

.logo_container:hover {
	text-shadow: 1px 1px 5px var(--highlight-color), 0 0 0.1em white, 0 0 0.5em white;
	color: var(--main-background-color);
	color: black;
}
.hamburger {
	display: none;
	cursor: pointer;
}
.bar {
	display: block;
	width: 25px;
	height: 3px;
	margin: 5px auto;
	transition: all 0.3s ease-in-out;
	background-color: var(--font-color);
}

@media screen and (max-width: 825px) {
	.nav_item {
		font-size: 16px;
	} 
}

@media screen and (max-width: 686px) {
	.logo_container {
		font-size: x-large;
	}
    .hamburger {
		display: block;
		right: 5%;
	}

	.hamburger.active .bar:nth-child(2) {
		opacity: 0;
	}

	.hamburger.active .bar:nth-child(1) {
		-webkit-transform: translateY(8px) rotate(45deg);
		        transform: translateY(8px) rotate(45deg);
	}

	.hamburger.active .bar:nth-child(3) {
		-webkit-transform: translateY(-8px) rotate(-45deg);
		        transform: translateY(-8px) rotate(-45deg);
	}

	.nav_list {
		display: none;
	}

	.navbar_nav2 {
		position: fixed;
		z-index: 3;
		right: 5%;
		padding-top: 15px;
		flex-direction: column;
		text-align: center;
	}

	.nav_link {
		font-size: 16px;
		padding: 15px 20px;
	}
	.nav_item {
		margin: 0px 0px 5px 0px;	
		width: 130px;
		background-color: var(--header-footer-color);
		border: 1px solid black;
	}
}

@media screen and (max-width: 390px) {
	.nav_list li {
		display: flex;
	}
	.nav_item {
		margin-bottom: 3px;
		font-size: 16px;
	}
}

footer {
	background-color: var(--header-footer-color);
	width: 100%;
	position:fixed;
	z-index: 2;
	bottom: 0px;
}
footer ul {
	display: flex;
	justify-content: center;
	color: var(--contrast-color);
	font-size: x-large;
}

footer li {
	text-decoration: none;
	padding: 10px 20px;
	list-style-type: none;
}

.footer {
	text-decoration: none;
}
.footer:hover {
	font-weight: bolder;
	color: var(--highlight-color);
}

.svgGit {
	vertical-align: -0.125em;
	width: 1.5em;
	height: 1.5em;
}

.svgGit:hover {
	fill: var(--highlight-color);
}

.svgLinkedIn {
	vertical-align: -0.125em;
	width: 1.5em;
	height: 1.5em;
}

.svgLinkedIn:hover {
	fill: var(--highlight-color);
}
.medium {
	font-weight: bold;
	color: white;
	font-size: 40px;
}

@media screen and (max-width: 825px) {
	footer {
		font-size: large;
	}
}

.hero_section {
	display: flex;
	margin-right: 20px;
	flex-direction: column;
	width: 100%;
	justify-content: center;
	text-align: right;
	overflow: none;
	height:100vh;
	background: url(/kyle-vance-portfolio/static/media/mainBackground.cacc3cd8.jpg) no-repeat center center fixed;
	background-size: cover;
	position: relative;
}
 
.hero_content {
	position: absolute;
	display: flex;
	margin-right: 20px;
	flex-direction: column;
	width: 95%;
	justify-content: center;
	text-align: right;
	z-index: 2;
}

.hero_content h1 {
	color: var(--font-color);
	font-weight: bolder;
	font-size: 70px;
}
.hero_content h2 {
	color: var(--font-color);
	font-weight: bolder;
	align-self: right;
	font-size: 50px;
	/* font-size: 70px; */
	padding-left: 60%;
	margin: 35px 0px;
}

.hero_content button {
	align-self: right;
	padding: 10px;
	font-size: 30px;
	border-radius: 0.5em;
	background-color: var(--contrast-color);
	font-weight: bolder;
}

.hero_content button:hover,
.hero_content button:focus {
	box-shadow: 3px 3px 10px 2px var(--highlight-color);
	-webkit-transform: translateY(-0.25em);
	        transform: translateY(-0.25em);
}

@media screen and (max-width: 1234px) {
	.hero_content {
		height: 70%;
	}
	.hero_content h1 {
		font-size: 45px;
	}
	.hero_content h2 {
		font-size: 35px;
	}
}

@media screen and (max-width: 968px) {
	.hero_content {
		height: 50%;
	}
	.hero_content h1 {
		font-size: 30px;
	}
	.hero_content h2 {
		font-size: 20px;
	}
}

@media screen and (max-width: 744px) {
	.hero_content {
		height: 30%;
	}
	.hero_content h1 {
		font-size: 30px;
	}
	.hero_content h2 {
		font-size: 20px;
	}
	.hero_content button {
		align-self: right;
		padding: 10px;
		font-size: 20px;
		border-radius: 0.5em;
		font-weight: bolder;
