/* Start Contact Information */
.contactMe {
	background-color: var(--main-background-color);
	display: flex;
	min-height: 100vh;
	height: 100%;
	align-items: center;
	justify-content: center;
	padding: 140px 5%;
}

.contactMe h2 {
	color: var(--font-color);
	font-weight: bolder;
	font-size: 50px;
}

.contactMe form {
	background-color: var(--header-footer-color);
	flex-direction: column;
	margin-right: 60px;
	padding: 40px 40px;
	justify-content: space-around;
	display: flex;
	border-radius: 1rem;
	width: 100%;
	/* height: 100%; */
	height: 575px;
}

.input_group {
	display: flex;
	width: 100%;
	color: var(--font-color);
	margin-top: 20px;
	justify-content: space-between;
}

.label {
	display: flex;
	width: 100%;
	justify-content: space-between;
}

.input {
	background: var(--main-background-color);
	border: none;
	color: var(--font-color);
	padding: 10px 10px 5px 5px;
	font-size: 30px;
	border-radius: 0.5rem;
	margin: 3px;
}

.input::-webkit-input-placeholder {
	font-size: 30px;
}

.input::placeholder {
	font-size: 30px;
}

textarea {
	background: var(--main-background-color);
	border: none;
	padding: 16px 178px 100px 5px;
	font-size: 30px;
	border-radius: 0.5rem;
}

textarea::-webkit-input-placeholder {
	color: var(--font-color);
}

textarea::placeholder {
	color: var(--font-color);
}

.input_group label {
	font-weight: bold;
	font-size: 30px;
}

.contactMe img {
	max-width: 40%;
	block-size: 575px;
	min-width: 575px;
	min-height: 500px;
	border-radius: 0.5rem;
}

.error-text {
	margin-top: 20px;
}

.contactMe button {
	margin-top: 20px;
	background: var(--contrast-color);
	color: black;
	border-radius: 0.5rem;
	font-size: 30px;
	width: 30%;
}

.contactMe button:hover,
.contactMe button:focus {
	background-color: var(--highlight-color);
}

.aTag {
	padding-top: 20px;
	color: var(--font-color);
	font-size: 25px;
}

.aTag a:hover {
	color: var(--highlight-color);
}

