/* Start Portfolio Section */
.portfolio {
	background: var(--main-background-color);
	padding: 120px var(--outside-padding);
	color: var(--font-color);
	align-items: center;
}

.portfolio h2 {
	margin-bottom: 50px;
	font-weight: bolder;
	font-size: xxx-large;
}

.projectGroupPortfolio {
	display: flex;
	flex-wrap: wrap;
	grid-gap: 80px;
	gap: 80px;
}

.projects {
	position: relative;
	width: 47%;
	min-width: 728px;
	min-height: 412px;
}

.projects img {
	width: 100%;
	height: 100%;
	border-radius: 0.5rem;
}

.projects .project_box {
	position: absolute;
	z-index: 1;
	background-color: black;
	margin-top: -87px;
	padding: 30px;
	border-radius: 0px 0.5rem 0px 0.5rem;
}

.projects .proTitle {
	font-size: 25px;
}
.projects p {
	margin-top: 10px;
	font-size: 20px;
}

.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 2;
	height: 100%;
	border-radius: 0.5rem;
	opacity: 0;
	color: white;
	background-color: #000000dd;
	line-height: 30px;
}
.overlay:hover {
	opacity: 1;
}
.overlayLink:hover {
	color: #3f89bb;
}

.overlayText {
	font-size: 20px;
	padding: 40px;
	font-weight: bolder;
}

.overlayTitle {
	color: var(--contrast-color);
	font-weight: bolder;
	font-size: larger;
}

.overlayLinks {
	margin-top: 20px;
	display: flex;
	justify-content: space-evenly;
	font-size: xx-large;
	font-weight: bold;
}
.overlayLinksPhone {
	display: none;
}

@media screen and (max-width: 1800px) {
	.overlay {
		line-height: 25px;
	}
	.overlayText {
