	}
}

@media screen and (max-width: 578px) {
	.projects .project_box {
		margin-top: -37px;
		padding: 10px;
	}
	.overlayTitle {
		font-size: 16px;
		padding: 0px;
	}
	.projects p {
		margin-top: 3px;
		font-size: 10px;
	}
	.projects {
		min-width: 345px;
		min-height: 265px;
	}
	.overlayLinks {
		display: none;
	}
	.overlayLinksPhone {
		margin: 30px;
		display: flex;
		justify-content: space-evenly;
		font-weight: bold;
		font-size: large;
	}
}

:root {
	--main-background-color: #4a4a4a;
	--header-footer-color: #0000007c;
	--font-color: #ffffff;
	--contrast-color: #bfd1e5;
	/* --highlight-color: #ccbc30; */
	--highlight-color: #c9c46f;
	--outside-padding: 5%;
}
* {
	font-family: 'Inter', sans-serif;
	box-sizing: border-box;
}



